import axios from 'axios';

const HSDKShipper_API_URL = 'https://localhost:44368/api/DKShipper' //'http://localhost:8082/api/HSShipper';

class HSShipper{
    getHoSoChuaDuyet(){
        return axios.get(HSDKShipper_API_URL + '?status=0');
    }

    getHoSoDaDuyet(){
        return axios.get(HSDKShipper_API_URL + '?status=1');
    }
}

export default new HSShipper();


