import axios from 'axios';

const HSDKBH_API_URL = 'https://localhost:44368/api/DKBH'//'http://localhost:8082/api/HSBanHang';

class HSBanHang{
    getHoSoChuaDuyet(){
        return axios.get(HSDKBH_API_URL + '?status=0');
    }

    getHoSoDaDuyet(){
        return axios.get(HSDKBH_API_URL + '?status=1');
    }
}

export default new HSBanHang();



