<template>
<!-- ============================================================== -->
<!-- Start right Content here -->
<!-- ============================================================== -->
<div class="content-page">
    <!-- Start content -->
    <div class="content">
        <div class="container-fluid">
            <div class="page-title-box">
                <div class="row align-items-center">
                    <div class="col-sm-6">
                        <h4 class="page-title">Thống kê phí hoa hồng</h4>
                    </div>
                </div>
                <!-- end row -->
            </div>
            <!-- end page-title -->

            <div class="row">
                <div class="col-lg-12">
                    <div class="card m-b-30">
                        <div class="card-body">                           
                    <div class="row">
                        <div class="col-sm-1">
                          <span> Tháng </span> 
                        </div>

                        <div class="col-sm-2">
                          <div class="dropdown mo-mb-2">
                            <div class="dropdown mo-mb-2">
                              <button class="btn btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                          {{tkhhMonth}}                                          
                              </button>
                              <div  class="dropdown-menu" aria-labelledby="dropdownMenuButton" >
                                <a class="dropdown-item" v-on:click="onTKHHMonthSelectAction(1)">1</a>
                                <a class="dropdown-item" v-on:click="onTKHHMonthSelectAction(2)">2</a>
                              </div>
                            </div>
                          </div>
                        </div>
                        <div class="col-sm-1">
                          <span> Năm </span> 
                        </div>

                        <div class="col-sm-2 mb-3">
                          <div class="dropdown mo-mb-2">
                            <div class="dropdown mo-mb-2">
                              <button class="btn btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                           {{tkhhYear}}
                              </button>
                              <div  class="dropdown-menu" aria-labelledby="dropdownMenuButton" >
                                <a class="dropdown-item" v-on:click="onTKHHYearSelectAction(2021)">2021</a>
                                <a class="dropdown-item" v-on:click="onTKHHYearSelectAction(2022)">2022</a>
                              </div>
                            </div>
                          </div>
                        </div>

                        <div class="col-sm-3">
                          <button type="button" class="btn btn-success waves-effect waves-light" @click="onGetTKHH()">Thống kê</button>
                        </div>
                    </div>
                            <div class="table-responsive">
                                <table class="table table-striped mb-0">
                                    <thead>
                                        <tr class="bg-primary">
                                            <th>#</th>
                                            <th>Người Bán</th>
                                            <th>Phí hoa hồng</th>
                                            <th>Tháng</th>
                                            <th>Năm</th>
                                        </tr>
                                    </thead>
                                    <tbody v-for="(nguoiban, index) in tkhh" :key="index">
                                        <tr>
                                            <th scope="row">{{index + 1}}</th>
                                            <td>{{nguoiban.nguoiBanId}}</td>
                                            <td>{{nguoiban.phiHoaHong}}</td>
                                            <td>{{nguoiban.thang}}</td>
                                            <td>{{nguoiban.nam}}</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <!-- end col -->
        </div>
    </div>
    <!-- container-fluid -->

    <!-- content -->

    <footer class="footer">
        © 2021 DiChoThue <span class="d-none d-sm-inline-block"><i class="mdi mdi-heart text-danger"></i></span>.
    </footer>
</div>
<!-- ============================================================== -->
<!-- End Right content here -->
<!-- ============================================================== -->
</template>

<script>
import axios from 'axios';
import {
  mapGetters,
    mapActions
} from 'vuex';


export default {
    name: 'CalcCommission',
  computed: {
            ...mapGetters([
               'tkhhMonth',
                'tkhhYear',
                'tkhh',
                // map getter tháng ở đây rồi lên kia vfor
            ]),
        },
        mounted() {
            // gọi hàm get tháng ở đây
            this.onGetTKHH();
        },
  methods: {
     onTKHHMonthSelectAction(month) {
             this.$store.dispatch('onTKHHMonthSelectAction', month)
           },
           onTKHHYearSelectAction(year) {
             this.$store.dispatch('onTKHHYearSelectAction', year)
           },
           onGetTKHH() {
             this.$store.dispatch('onGetTKHHAction')
           },
  },
  data () {
    return {
     accountJson: "",
    }
  },
}

</script>


