<template>
<!-- ============================================================== -->
<!-- Start right Content here -->
<!-- ============================================================== -->
<div class="content-page">
    <!-- Start content -->
    <div class="content">
        <div class="container-fluid">
            <div class="page-title-box">
                <div class="row align-items-center">
                    <div class="col-sm-6">
                        <h4 class="page-title">Thống kê số lượng khách hàng/cửa hàng/shipper</h4>
                    </div>
                </div>
                <!-- end row -->
            </div>
            <!-- end page-title -->

            <div class="row">
              <div class="col-xl-6">
                <div class="card m-b-30">
                  <div class="card-body">
                    <h4 class="mt-0 header-title">Khách Hàng</h4>
                    <div  class="col-xl-12" v-if="!loadingData">
                      <GChart type="PieChart" :data="chartData1" :options="chartOptions" :settings="{packages: ['corechart']}" />           
                    </div>
                 </div>
              </div>
            </div> <!-- end col -->

            <div class="col-xl-6">
              <div class="card m-b-30">
                <div class="card-body">
                  <h4 class="mt-0 header-title">Cửa hàng</h4>
                  <div   class="col-xl-12" v-if="!loadingData">
                    <GChart type="PieChart" :data="chartData2" :options="chartOptions" :settings="{packages: ['corechart']}" />       
                  </div>
                </div>
            </div>
          </div> <!-- end col -->
          <div class="col-xl-3"/>
            <div class="col-xl-6">
              <div class="card m-b-30">
                <div class="card-body">       
                  <h4 class="mt-0 header-title">Shipper</h4>
                  <div   class="col-xl-12" v-if="!loadingData">
                    <GChart type="PieChart" :data="chartData3" :options="chartOptions" :settings="{packages: ['corechart']}" />                              
                  </div>
                </div>
              </div>
          </div> <!-- end col -->
          <div class="col-xl-3"/>
        </div>
            <!-- end col -->
        </div>
    </div>
    <!-- container-fluid -->

    <!-- content -->

    <footer class="footer">
        © 2021 DiChoThue <span class="d-none d-sm-inline-block"><i class="mdi mdi-heart text-danger"></i></span>.
    </footer>
</div>
<!-- ============================================================== -->
<!-- End Right content here -->
<!-- ============================================================== -->
</template>

<script>
import {GChart} from 'vue-google-charts'
import axios from 'axios';

export default {
  name: 'PieChartContainer',
  components: {
    GChart
  },
  created() {
    this.fetchData();
  },
   methods: {
    fetchData() {
      let url = 'https://localhost:44368/api/KhachHang';
      axios.get(url).then((response) => {
        let chartDataJson = response.data;
       
        for (let i = 0; i < chartDataJson.length; ++i) {
          let item = chartDataJson[i];
          this.chartData1.push([item.CapDoVung,item.SoLuong]);
        }
        this.loadingData = false;

      });
      let url2 = 'https://localhost:44368/api/NguoiBan';
      axios.get(url2).then((response) => {
        let chartDataJson2 = response.data;
       
        for (let i = 0; i < chartDataJson2.length; ++i) {
          let item = chartDataJson2[i];
          this.chartData2.push([item.CapDoVung,item.SoLuong]);
        }
        this.loadingData = false;

      });

      let url3 = 'https://localhost:44368/api/Shipper';
      axios.get(url3).then((response) => {
        let chartDataJson3 = response.data;
       
        for (let i = 0; i < chartDataJson3.length; ++i) {
          let item = chartDataJson3[i];
          this.chartData3.push([item.CapDoVung,item.SoLuong]);
        }
        this.loadingData = false;

      });
    },
   },
  data() {
    return {
      loadingData: true,
      chartData1: [['CapDoVung', 'SoLuong'],],
       chartData2: [['CapDoVung', 'SoLuong'],],
        chartData3: [['CapDoVung', 'SoLuong'],],
      chartOptions: {
        chart: {
          title: "My Daily Activities",
          pieHole: 0.4,
          width: 600,
          height: 600,
        },
        slices: [{color: 'red'}, {color: 'yellow'}, {color: 'green'}]
      },
    }
  }
}
</script>


