import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios';

Vue.use(Vuex)

// B1: tạo state
const thongKeDoanhThuStore = {
    state: () => ({
         month: 1,
         year: 2021, 
         chartData: [],
        }),
  };


  const thongKeHoaHongStore = {
    state: () => ({
         month: 1,
         year: 2021, 
         statisticData: [],
        }),
  };

export const store = new Vuex.Store({
    modules: {
        //B2: import module
        thongKeDoanhThuStore: thongKeDoanhThuStore,
        thongKeHoaHongStore: thongKeHoaHongStore,
    },
    state: {
        count: 0
      },
      // B3: Tạo mutation
      mutations: {
        onTKDTMonthSelect (state, month) {
         state.thongKeDoanhThuStore.month = month
        },

        onTKDTYearSelect (state, year) {
            state.thongKeDoanhThuStore.year = year
           },

           onGetTKDT(state, chartData) {
               state.thongKeDoanhThuStore.chartData = chartData
           },
           onTKHHMonthSelect (state, month) {
            state.thongKeHoaHongStore.month = month
           },
   
           onTKHHYearSelect (state, year) {
               state.thongKeHoaHongStore.year = year
              },
   
              onGetTKHH(state, statisticData) {
                  state.thongKeHoaHongStore.statisticData = statisticData
              }
      },
      // B4: Tạo action (cái này sẽ gọi bên vue)
      actions: {
        onTKDTMonthSelectAction ({commit}, month) {
          commit('onTKDTMonthSelect', month)
        },

        onTKDTYearSelectAction ({commit}, year) {
            commit('onTKDTYearSelect', year)
          },      
          onGetTKDTAction({commit, state}) {
            let url = 'http://localhost:8082/api/doanhthu';
            let chartDataRes = [['NguoiBanId', 'DoanhThu'],];

            
              url += '?thang=' + state.thongKeDoanhThuStore.month;
              url += '&nam=' + state.thongKeDoanhThuStore.year;
    
            console.log(url);
      
            axios.get(url).then((response) => {
              let chartDataJson = response.data;
      
              console.log(chartDataJson);
             
              for (let i = 0; i < chartDataJson.length; ++i) {
                let item = chartDataJson[i];
                chartDataRes.push([item.nguoiBanId,item.doanhThu]);
              }

              console.log(chartDataRes);
              commit('onGetTKDT', chartDataRes)
            });
          },

          onTKHHMonthSelectAction ({commit}, month) {
            commit('onTKHHMonthSelect', month)
          },
  
          onTKHHYearSelectAction ({commit}, year) {
              commit('onTKHHYearSelect', year)
            },      
            onGetTKHHAction({commit, state}) {
              let url = 'http://localhost:8082/api/phihoahong';
            
  
              
                url += '?thang=' + state.thongKeHoaHongStore.month;
                url += '&nam=' + state.thongKeHoaHongStore.year;
      
              console.log(url);
        
              axios.get(url).then((response) => {
                
                console.log(response.data);
                commit('onGetTKHH', response.data)
              });
            }
      },
      // B5: Tạo getter (vue get mấy cái state ra)
      getters: {
        tkdtMonth (state) {
          return state.thongKeDoanhThuStore.month
        },
        tkdtYear (state) {
            return state.thongKeDoanhThuStore.year
        },
        tkdt (state) {
            return state.thongKeDoanhThuStore.chartData
        },


        tkhhMonth (state) {
          return state.thongKeHoaHongStore.month
        },
        tkhhYear (state) {
            return state.thongKeHoaHongStore.year
        },
        tkhh (state) {
            return state.thongKeHoaHongStore.statisticData
        },
      }
})



