<template>
<!-- ============================================================== -->
<!-- Start right Content here -->
<!-- ============================================================== -->
<div class="content-page">
    <!-- Start content -->
    <div class="content">
        <div class="container-fluid">
            <div class="page-title-box">
                <div class="row align-items-center">
                    <div class="col-sm-6">
                        <h4 class="page-title">Quản Lý Tài Khoản</h4>
                    </div>
                </div>
                <!-- end row -->
            </div>
            <!-- end page-title -->

            <div class="row">
                <div class="col-lg-12">
                    <div class="card m-b-30">
                        <div class="card-body">
                            <div class="row p-3">
                                <div class="col-lg-4">
                                    <form role="search" class="email-inbox">
                                        <div class="form-group mb-0">
                                            <input type="text" class="form-control rounded" placeholder="Search accounts.." />
                                            <button type="submit"><i class="fa fa-search"></i></button>
                                        </div>
                                    </form>
                                </div>
                            </div>

                            <div class="table-responsive">
                                <table class="table table-striped mb-0">
                                    <thead>
                                        <tr class="bg-primary">
                                            <th>#</th>
                                            <th>CMND</th>
                                            <th>Họ tên</th>
                                            <th>Ngày sinh</th>
                                            <th>Số điện thoại</th>
                                            <th>Email</th>
                                            <th>Giới tính</th>
                                            <th>Username</th>
                                            <th>Password</th>
                                            <th>Trạng thái</th>
                                            <th>Actions</th>
                                        </tr>
                                    </thead>
                                    <tbody v-for="(nguoidung, index) in accountJson" :key="index">
                                        <tr>
                                            <th scope="row">{{index + 1}}</th>
                                            <td>{{nguoidung.CMND}}</td>
                                            <td>{{nguoidung.HoTen}}</td>
                                            <td>{{nguoidung.NgaySinh}}</td>
                                            <td>{{nguoidung.SoDienThoai}}</td>
                                            <td>{{nguoidung.Email}}</td>
                                            <td>{{nguoidung.GioiTinh}}</td>
                                            <td>{{nguoidung.Username}}</td>
                                            <td>{{nguoidung.Password}}</td>
                                            <td>
                                              <div v-if="nguoidung.TrangThai === LOCKED_STATE">
                                                <span class="badge badge-danger">{{nguoidung.TrangThai}}</span>
                                              </div>
                                              <div v-else>
                                                 <span class="badge badge-success">{{nguoidung.TrangThai}}</span>
                                              </div>
                                            </td>

                                            <td>
                                                <div class="btn-toolbar form-group mb-0">
                                                    <div class="row">
                                                         <div v-if="nguoidung.TrangThai === 'Bi khoa'">
                                                            <a href="#" role="button" class="btn btn-success waves-effect waves-light m-r-5" v-on:click = "updateAccountState(nguoidung)"><i class="mdi mdi-lock-open-outline"></i></a>
                                                        </div>

                                                        <div v-else>
                                                            <a href="#" role="button" class="btn btn-info waves-effect waves-light m-r-5" v-on:click = "updateAccountState(nguoidung)"><i class="mdi mdi-lock"></i></a>
                                                        </div>
                                                        
                                                        <button type="button" class="btn btn-danger waves-effect waves-light m-r-5" data-toggle="modal" data-target="#alertDeleteModal" v-on:click = "deleteAccount(nguoidung.CMND)"><i class="far fa-trash-alt"></i></button>
                                                    </div>
                                                </div>
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <!-- end col -->
        </div>
    </div>
    <!-- container-fluid -->

    <!-- content -->

    <footer class="footer">
        © 2021 DiChoThue <span class="d-none d-sm-inline-block"><i class="mdi mdi-heart text-danger"></i></span>.
    </footer>
</div>
<!-- ============================================================== -->
<!-- End Right content here -->
<!-- ============================================================== -->
</template>

<script>
import axios from 'axios';

export default {
    name: 'AccountManagement',
  mounted() {
    this.fetchData();
  },
  created() {
    this.LOCKED_STATE = 'Bi khoa';
    this.ACTIVE_STATE = 'Dang hoat dong';
  },
  methods: {
    fetchData() {
      let url = 'https://localhost:44368/api/NguoiDung';
      axios.get(url).then((response) => {
       this.accountJson = response.data; 
      });
    },

    deleteAccount(cmnd){
        let url = `https://localhost:44368/api/NguoiDung/${cmnd}`;
       
        axios.delete(url, cmnd).then(response => {
            if (response.data == 'success') {
                // Tạm thời
                this.$router.go();
            }
        }) 
        .catch(error => {
            this.errorMessage=error.message; 
            console.error("There was an error", error);
        });
    },

    updateAccountState(account){
        if (account.TrangThai === this.LOCKED_STATE) {
            account.TrangThai = this.ACTIVE_STATE;
        } else {
            account.TrangThai = this.LOCKED_STATE;
        }

        let url = 'https://localhost:44368/api/NguoiDung';
        axios.put(url, account).then(response => {
           console.log(response.data);
        }) 
        .catch(error => {
            this.errorMessage=error.message; 
            console.error("There was an error", error);
        });
    }
  },
  data () {
    return {
     accountJson: "",
    }
  },
}

</script>


