<template>
<!-- ============================================================== -->
<!-- Start right Content here -->
<!-- ============================================================== -->
<div class="content-page">
    <!-- Start content -->
    <div class="content">
        <div class="container-fluid">
            <div class="page-title-box">
                <div class="row align-items-center">
                    <div class="col-sm-6">
                        <h4 class="page-title">Danh sách hồ sơ đăng ký bán hàng</h4>
                    </div>
                
                </div>
                <!-- end row -->
            </div>
            <!-- end page-title -->

            <div class="row">
                <div class="col-xl-12">
                    <div class="row">
                        <div class="card m-b-30">
                            <div class="card-header">
                                <div>
                                    <button type="button" class="btn btn-success waves-effect waves-light">Hồ sơ chưa duyệt</button>
                                </div>
                            </div>
                            <div class="card-body">
                                <div class="table-responsive">
                                <table class="table table-striped mb-0">
                                    <thead>
                                        <tr class="bg-primary">
                                            <th>Mã hồ sơ</th>
                                            <th>Tên cửa hàng</th>
                                            <th>Chủ sở hữu</th>
                                            <!-- <th>CMND</th> -->
                                            <th>SĐT</th>
                                            <th>Chi tiết</th>
                                            <th>Actions</th>
                                        </tr>
                                    </thead>
                                    <tbody v-for="(hs, index) in hoSo0" :key="index">
                                        <tr>
                                            <td>{{hs.HoSoDangKyBanHangId}}</td>
                                            <td>{{hs.TenCuaHang}}</td>
                                            <td>{{hs.HoTen}}</td>
                                            <td>{{hs.SDT}}</td>
                                            <td>
                                                <details>
                                                    <summary>
                                                        <strong>Xem chi tiết</strong>
                                                    </summary>
                                                    <p>
                                                        <strong>CMND: </strong>{{hs.CMND}}<br>
                                                        <strong>Email: </strong>{{hs.Email}}<br>
                                                        <strong>Địa chỉ: </strong>{{hs.DiaChi}}<br>
                                                        <strong>Hình ảnh Giấy phép kinh doanh:</strong><br>
                                                        <a>
                                                            <img :src="PhotoPath + hs.ImageGiayPhepKD" style="max-width:500px;width:100%"/>
                                                        </a><br>
                                                        <strong>Hình ảnh Giấy chứng nhận ATVS thực phẩm:</strong><br>
                                                        <a>
                                                            <img :src="PhotoPath + hs.ImageGiayCNATVSTP" style="max-width:500px;width:100%"/>
                                                        </a><br>
                                                    </p>
                                                </details>
                                            </td>
                                             <td>
                                                <div class="btn-toolbar form-group mb-0">
                                                    <div class="row">
                                                        <div class="card-img text-center">
                                                            <button type="button " class="btn btn-success" @click="chapNhan(hs)">Chấp nhận</button>
                                                        </div>
                                                        <div class="card-img text-center"> 
                                                            <button type="button" class="btn btn-danger" @click="tuChoi(hs)">Từ chối</button>
                                                        </div>
                                                    </div>
                                                </div>
                                             </td>
                                        </tr>
                                    </tbody>
                                </table>
                                </div>
                            </div>
                        </div>
                        
                        <div class="card m-b-30">
                            <div class="card-header">
                                <div>
                                    <button type="button" class="btn btn-success waves-effect waves-light">Hồ sơ đã duyệt</button>
                                </div>
                            </div>

                            <div class="card-body">
                                <div class="table-responsive">
                                <table class="table table-striped mb-0">
                                    <thead>
                                        <tr class="bg-primary">
                                            <th>Mã hồ sơ</th>
                                            <th>Tên cửa hàng</th>
                                            <th>Chủ sở hữu</th>
                                            <!-- <th>CMND</th> -->
                                            <th>SĐT</th>
                                            <th>Trạng thái</th>
                                            <th>Chi tiết</th>
                                            <th>Actions</th>
                                        </tr>
                                    </thead>
                                    <tbody v-for="(hs, index) in hoSo1" :key="index">
                                        <tr>
                                            <td>{{hs.HoSoDangKyBanHangId}}</td>
                                            <td>{{hs.TenCuaHang}}</td>
                                            <td>{{hs.HoTen}}</td>
                                            <td>{{hs.SDT}}</td>
                                            <td>
                                                <div v-if="hs.TrangThai == '-1'">
                                                    <span class="badge badge-danger">Đã từ chối</span>
                                                </div>
                                                <div v-else>
                                                    <span class="badge badge-success">Đã chấp nhận</span>
                                                </div>
                                            </td>
                                            <td>
                                                <details>
                                                    <summary>
                                                        <strong>Xem chi tiết</strong>
                                                    </summary>
                                                    <p>
                                                        <strong>CMND: </strong>{{hs.CMND}}<br>
                                                        <strong>Email: </strong>{{hs.Email}}<br>
                                                        <strong>Địa chỉ: </strong>{{hs.DiaChi}}<br>
                                                        <strong>Hình ảnh Giấy phép kinh doanh:</strong><br>
                                                        <a>
                                                            <img :src="PhotoPath + hs.ImageGiayPhepKD" style="max-width:500px;width:100%"/>
                                                        </a><br>
                                                        <strong>Hình ảnh Giấy chứng nhận ATVS thực phẩm:</strong><br>
                                                        <a>
                                                            <img :src="PhotoPath + hs.ImageGiayCNATVSTP" style="max-width:500px;width:100%"/>
                                                        </a><br>
                                                    </p>
                                                </details>
                                            </td>
                                             <td>
                                                <div class="btn-toolbar form-group mb-0">
                                                    <div class="row">
                                                        <div v-if="hs.TrangThai == '-1'">
                                                            <div class="card-img text-center">
                                                                <button type="button " class="btn btn-success" @click="chapNhan(hs)">Chấp nhận</button>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                             </td>
                                        </tr>
                                        
                                    </tbody>
                                </table>
                                </div>
                            </div>
                        </div>
                    </div>

                    
                </div> <!-- end col -->
         
            </div>
            <!-- end col -->

        </div>
    </div>
    <!-- container-fluid -->

    <!-- content -->

    <footer class="footer">
        © 2021 DiChoThue <span class="d-none d-sm-inline-block"><i class="mdi mdi-heart text-danger"></i></span>.
    </footer>
</div>
<!-- ============================================================== -->
<!-- End Right content here -->
<!-- ============================================================== -->
</template>

<script>
import HSBanHang from '../router/HoSoDKBH'
import axios from 'axios'

export default {
    name: "DuyetHoSoDKBH",

    data(){
        return{
            hoSo1: [],
            hoSo0: [],
            PhotoPath: "http://127.0.0.1:8081/" //Thay link server hình ảnh vào đây
        }
    },

    created() {
            this.GetHoSoChuaDuyet();
            this.GetHoSoDaDuyet();
        },

    methods: {
        GetHoSoChuaDuyet(){
            HSBanHang.getHoSoChuaDuyet().then((Response) => {
                this.hoSo0 = Response.data;
            })
        },

        GetHoSoDaDuyet(){
            HSBanHang.getHoSoDaDuyet().then((Response) => {
                this.hoSo1 = Response.data;
            })
        },

        chapNhan(HoSo){
            HoSo.TrangThai = "1";
            axios.put("https://localhost:44368/api/DKBH/" + HoSo.HoSoDangKyBanHangId,HoSo)
            .then((Response)=>{
                this.GetHoSoChuaDuyet();
                this.GetHoSoDaDuyet();
                alert(Response.data)
            });

            let pass = ""
    	    let chars = "abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789"
    
            for( let i=0; i < 6; i++ ) {
				pass += chars.charAt(Math.floor(Math.random() * chars.length))
            }
            
            axios.post("https://localhost:44368/api/NguoiDung",{
                CMND: HoSo.CMND,
                HoTen: HoSo.HoTen,
                NgayCapCMND: "1/1/2015",
                NoiCapCMND: HoSo.DiaChi,
                NgaySinh: "1/1/1995",
                DiaChi: HoSo.DiaChi,
                Email: HoSo.Email,
                SoDienThoai: HoSo.SDT,
                CapDoVung: "Xanh",
                GioiTinh: "Nam",
                Username: HoSo.CMND,
                Password: pass,
                TrangThai: "Dang hoat dong"
            })
            .then((Response)=>{
                if (Response.data == "")
                    alert("Chưa tạo tài khoản người dùng thành công!")
            });

            axios.post("https://localhost:44368/api/NguoiBan",{
                TenCuaHang: HoSo.TenCuaHang,
                HoSoDangKyBanHangId: HoSo.HoSoDangKyBanHangId,
                CMND: HoSo.CMND
            })
            .then((Response)=>{
                if (Response.data == "")
                    alert("Chưa tạo tài khoản người bán thành công!")
            });
        },

        tuChoi(HoSo){
            HoSo.TrangThai = "-1";
            axios.put("https://localhost:44368/api/DKBH/" + HoSo.HoSoDangKyBanHangId,HoSo)
            .then((Response)=>{
                this.GetHoSoChuaDuyet();
                this.GetHoSoDaDuyet();
                alert(Response.data)
            })
        }
    }
};


</script>



