<template>
<!-- ============================================================== -->
<!-- Start right Content here -->
<!-- ============================================================== -->
<div class="content-page">
    <!-- Start content -->
    <div class="content">
        <div class="container-fluid">
            <div class="page-title-box">
                <div class="row align-items-center">
                    <div class="col-sm-6">
                        <h4 class="page-title">Thống kê doanh thu cửa hàng</h4>
                    </div>
                
                </div>
                <!-- end row -->
            </div>
            <!-- end page-title -->

            <div class="row">
              <div class="col-xl-12">
                <div class="card m-b-30">
                  <div class="card-body">
                    <div class="row">
                        <h4 class="col-sm-3 mt-0 header-title">Doanh Thu</h4>

                        <div class="col-sm-1">
                          <span> Tháng </span> 
                        </div>

                        <div class="col-sm-2">
                          <div class="dropdown mo-mb-2">
                            <div class="dropdown mo-mb-2">
                              <button class="btn btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                          {{tkdtMonth}}                                          
                              </button>
                              <div  class="dropdown-menu" aria-labelledby="dropdownMenuButton" >
                                <a class="dropdown-item" v-on:click="onTKDTMonthSelectAction(1)">1</a>
                                <a class="dropdown-item" v-on:click="onTKDTMonthSelectAction(2)">2</a>
                              </div>
                            </div>
                          </div>
                        </div>

                        <div class="col-sm-1">
                          <span> Năm </span> 
                        </div>

                        <div class="col-sm-2">
                          <div class="dropdown mo-mb-2">
                            <div class="dropdown mo-mb-2">
                              <button class="btn btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                           {{tkdtYear}}
                              </button>
                              <div  class="dropdown-menu" aria-labelledby="dropdownMenuButton" >
                                <a class="dropdown-item" v-on:click="onTKDTYearSelectAction(2021)">2021</a>
                                <a class="dropdown-item" v-on:click="onTKDTYearSelectAction(2022)">2022</a>
                              </div>
                            </div>
                          </div>
                        </div>
                        <div class="col-sm-3">
                          <button type="button" class="btn btn-success waves-effect waves-light" @click="onGetTKDT()">Thống kê</button>
                        </div>

                        </div>
                           <div class="m-b-15"/>
                              <div v-if="tkdt.length > 1">
                                <GChart type="BarChart" :data="tkdt" :options="chartOptions" :settings="{packages: ['corechart']}" />           
                            </div>
                            <div v-else>
                            <h4 class="col-sm-12 text-center m-30">Không có dữ liệu</h4>
                            </div>
                            </div>
                          </div>
                        </div> <!-- end col -->
                      <div class="col-xl-3"/>
                    </div>
            <!-- end col -->
                  </div>
                </div>
    <!-- container-fluid -->

    <!-- content -->

    <footer class="footer">
        © 2021 DiChoThue <span class="d-none d-sm-inline-block"><i class="mdi mdi-heart text-danger"></i></span>.
    </footer>
</div>
<!-- ============================================================== -->
<!-- End Right content here -->
<!-- ============================================================== -->
</template>

<script>
import { GChart } from "vue-google-charts";
import axios from 'axios';
// B1: Import cái này
import {
  mapGetters,
    mapActions
} from 'vuex';

export default {
  name: "BarChart",
  components: {
    GChart
  },
  // B2: Mapp getter
  computed: {
            ...mapGetters([
               'tkdtMonth',
                'tkdtYear',
                'tkdt',
            ]),
        },
        mounted() {
            this.onGetTKDT();
        },
   methods: {
     // B3: Mapp action
      onTKDTMonthSelectAction(month) {
             this.$store.dispatch('onTKDTMonthSelectAction', month)
           },
           onTKDTYearSelectAction(year) {
             this.$store.dispatch('onTKDTYearSelectAction', year)
           },
           onGetTKDT() {
             this.$store.dispatch('onGetTKDTAction')
           },
   },
  data() {
    return {  
      chartOptions: {
        chart: {
          title: "Thống kê doanh thu người bán",
        
        },
          height: 600
      },
    };
  }
};
</script>


