import Vue from 'vue'
import Router from 'vue-router'
import AccountManagement from '@/pages/AccountManagement'
import PieChartContainer from '@/pages/PieChartContainer'
import BarChart from '@/pages/BarChart'

import DuyetHoSoDKBH from '@/pages/DuyetHoSoDKBH'
import DuyetHoSoShipper from '@/pages/DuyetHoSoShipper'
import CalcCommission from '@/pages/CalcCommission'


Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/CalcCommission',
            name: 'CalcCommission',
            component: CalcCommission
        },
        {
            path: '/BarChart',
            name: 'BarChart',
            component: BarChart
        },
        {
            path: '/PieChartContainer',
            name: 'PieChartContainer',
            component: PieChartContainer
        },
        {
        path: '/',
        name: 'AccountManagement',
        component: AccountManagement
        },
        {
            path: '/DuyetHoSoDKBH',
            name: 'DuyetHoSoDKBH',
            component: DuyetHoSoDKBH
        },{
            path: '/DuyetHoSoShipper',
            name: 'DuyetHoSoShipper',
            component: DuyetHoSoShipper
        }
]
})


